import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import MathJax from 'react-mathjax2';

export default class ExCircuit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        Circuit
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
          Circuit
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
            A two-dimensional and nonlinear example about a tunnel-diode oscillator circuit. It models the voltage drop {mjax('V')} and the current {mjax('I')}.
            Taken from <Link href={'http://hsolver.sourceforge.net/benchmarks/benchmarks_5.html'} target='_blank' rel='noreferrer'>HSolver benchmarks</Link>.
            </p>
            <h3>Dynamics</h3>
            <table>
              <tr><td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 1</h4></td></tr>
              <tr>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{                                     \n' +
                          '\\begin{array}{ll}                            \n' +
                          '-0.1   \\!\\!\\!\\!&\\leq V \\leq 0.055 \\\\  \n' +
                          '-0.002 \\!\\!\\!\\!&\\leq I \\leq 0.002 \\\\  \n' +
                          '\\end{array}                                  \n' +
                          '\\right.                                      \n'
                         )}
                </td>
              </tr>
              <tr>
                <td>Flow:</td>
                <td>{mjax('\\left\\{                                                                 \n' +
                          '\\begin{array}{l}                                                         \n' +
                          '\\dot V = 1000000000000( - (6.0105 V^3 - 0.9917V^2 + 0.0545V) + I) \\\\   \n' +
                          '\\dot I = 1000000(-V - 200I + 0.3)                                 \\\\   \n' +
                          '\\end{array}                                                              \n' +
                          '\\right.                                                                  \n'
                         )}
                </td>
              </tr>
              <tr><td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 2</h4></td></tr>
              <tr>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{                                     \n' +
                          '\\begin{array}{ll}                            \n' +
                          '0.055  \\!\\!\\!\\!&\\leq V \\leq 0.35  \\\\  \n' +
                          '-0.002 \\!\\!\\!\\!&\\leq I \\leq 0.002 \\\\  \n' +
                          '\\end{array}                                  \n' +
                          '\\right.                                      \n'
                         )}
                </td>
              </tr>
              <tr>
                <td>Flow:</td>
                <td>{mjax('\\left\\{                                                                             \n' +
                          '\\begin{array}{l}                                                                     \n' +
                          '\\dot V = 1000000000000*( - (0.0692V^3 - 0.0421V^2 + 0.004V + 0.00089579) + I) \\\\   \n' +
                          '\\dot I = 1000000(-V - 200I + 0.3)                                             \\\\   \n' +
                          '\\end{array}                                                                          \n' +
                          '\\right.                                                                              \n'
                         )}
                </td>
              </tr>
              <tr><td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 3</h4></td></tr>
              <tr>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{                                     \n' +
                          '\\begin{array}{ll}                            \n' +
                          '0.035  \\!\\!\\!\\!&\\leq V \\leq 0.6   \\\\  \n' +
                          '-0.002 \\!\\!\\!\\!&\\leq I \\leq 0.002 \\\\  \n' +
                          '\\end{array}                                  \n' +
                          '\\right.                                      \n'
                         )}
                </td>
              </tr>
              <tr>
                <td>Flow:</td>
                <td>{mjax('\\left\\{                                                                          \n' +
                          '\\begin{array}{l}                                                                  \n' +
                          '\\dot V = 1000000000000*( - (0.2634V^3 - 0.2765V^2 + 0.0968V - 0.0112) + I) \\\\   \n' +
                          '\\dot I = 1000000*( - V - 200I + 0.3)                                       \\\\   \n' +
                          '\\end{array}                                                                       \n' +
                          '\\right.                                                                           \n'
                         )}
                </td>
              </tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Edge</h4></td>
              </tr>
              <tr><td>Source:     </td><td>Mode 1</td></tr>
              <tr><td>Destination:</td><td>Mode 2</td></tr>
              <tr><td>Relation:</td><td>{mjax('V \\geq 0.055')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('I \\geq 0.0006')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('V\'=V')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('I\'=I')}</td></tr>
              <tr><td>&nbsp;</td></tr>
              <tr><td>Source:     </td><td>Mode 2</td></tr>
              <tr><td>Destination:</td><td>Mode 1</td></tr>
              <tr><td>Relation:</td><td>{mjax('V \\leq 0.055')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('I \\leq 0.0006')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('V\'=V')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('I\'=I')}</td></tr>
              <tr><td>&nbsp;</td></tr>
              <tr><td>Source:     </td><td>Mode 2</td></tr>
              <tr><td>Destination:</td><td>Mode 3</td></tr>
              <tr><td>Relation:</td><td>{mjax('V \\geq 0.35')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('I \\geq 0.0006')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('V\'=V')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('I\'=I')}</td></tr>
              <tr><td>&nbsp;</td></tr>
              <tr><td>Source:     </td><td>Mode 3</td></tr>
              <tr><td>Destination:</td><td>Mode 2</td></tr>
              <tr><td>Relation:</td><td>{mjax('V \\leq 0.35')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('I \\leq 0.0006')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('V\'=V')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('I\'=I')}</td></tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Initial States</h4></td>
              </tr>
              <tr><td>Mode 3:     </td><td>{mjax('V \\geq 0.42 \\wedge V \\leq 0.52 \\wedge I = 0.0006')}</td></tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Unsafe States</h4></td>
              </tr>
              <tr><td>Mode 1: </td><td>{mjax('I<-0.0015 \\vee I>0.00175 \\vee V<-0.04')}</td></tr>
              <tr><td>Mode 2: </td><td>{mjax('I<-0.0015 \\vee I>0.00175')}</td></tr>
              <tr><td>Mode 3: </td><td>{mjax('I<-0.0015 \\vee I>0.00175 \\vee V>0.54')}</td></tr>
            </table>
            <h4 style={{marginBottom:'0'}}>Simulation</h4>
            <p align='justify'>Numbers used in this model are too big and most simulators cannot handle them.The following is the simulation for 0.001 units of time.</p>
            <Card>
              <CardContent style={{padding:'0'}}>
                <Grid container spacing={0} alignItems="stretch">
                  <Grid item xs={12} align='center'>
                    <Typography style={{padding:'0', overflowX:'auto'}}>
                      <img src="./images/img - Circuit.png" alt="Circuit Simulation"/>
                    </Typography>
                  </Grid>
                  <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>Simulation Generated by Matlab<sup>&reg;</sup></Grid>
                </Grid>
              </CardContent>
            </Card>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}

function mjax(fml) {
  return  (
    <MathJax.Context input='tex'>
      <MathJax.Node inline>{fml}</MathJax.Node>
    </MathJax.Context>
  );
}