import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

export default class ExHeater extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        Heater
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
        Heater
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
              Heater is another well-known example in the world of hybrid automata benchmarks
              (see {' '}
               <Link href="https://doi.org/10.1007/978-3-540-24743-2_22"                      target='_blank' rel='noreferrer'>Fehnker et. al.</Link> and {' '}
               <Link href="https://swt.informatik.uni-freiburg.de/tool/spaceex/guided-search" target='_blank' rel='noreferrer'>SpaceEx Guided Search</Link> {' '}
               for examples).
              There is a house with three rooms that are heated by a small number of heaters.
              The temperature in each room depends on the temperature of the adjacent rooms, 
              on the outside temperature, and on whether a heater is turned on in the room.
              The heater is controlled by a typical thermostat, i.e., it is switched on if the temperature is 
              below a certain threshold, and off if it is beyond another (higher) threshold.
              When the temperature in a room falls below a certain level, it may get a heater from one of the adjacent rooms, 
              provided that the temperature in this room is significantly higher. In this way the heaters are shared by the 
              different rooms to maintain some minimum temperature in all rooms.
              Please see <Link href="https://doi.org/10.1007/978-3-540-24743-2_22" target='_blank' rel='noreferrer'>Fehnker et. al.</Link> {' '}
              for more detail.
            </p>
            <p align='justify'>
              The following figure shows an example for three rooms that are heated by two heaters.
            </p>
            <Card>
            <CardContent style={{padding:'0'}}>
              <Grid container spacing={0} alignItems="stretch">
                <Grid item xs={12} align='center'>
                  <Typography style={{padding:'0', overflowX:'auto'}}>
                    <img src="./images/img - Heater - Ex.png" alt="Heater Example" style={{width:'600px'}}/>
                  </Typography>
                </Grid>
                <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>
                  Borrowed from {' '}
                  <Link href="https://doi.org/10.1007/978-3-540-24743-2_22" target='_blank' rel='noreferrer' style={{color:'white'}}>Benchmarks for Hybrid Systems Verification, by Fehnker et. al.</Link>
                </Grid>
              </Grid>
            </CardContent>
            </Card>
            <p align='justify'>
              We have a total of 6 Heater examples in our benchmarks.
              All of them have 4 locations, and 6 edges, and 3 continuous variables.
              Every location has an unsafe set as well as an initial set, except in Heater-3 where only one location 
              has an initial set. 
              Despite its small size, this is one of the most challenging set of examples 
              (for both our tool and those that we used in our comparison).
            </p>
            <h4 style={{marginBottom:'0'}}>Simulation</h4>
            <p align="justify">
              We used <Link href='http://spaceex.imag.fr/' target='_blank' rel='noreferrer'>SpaceEx</Link> and the Heater-04 problem for this section.
              The first three figures below show the simulation of an execution (there is only one initial state in this example). 
              We set local time horizon to 10 and sampling time to 10000. 
              The tool found fixed point after 7 iterations.
              The last three figures show the reachable set using {' '}
              <Link href="http://spaceex.imag.fr/news/new-analysis-algorithm-stc-scenario-released-47" target='_blank' rel='noreferrer'>STC Support Function</Link> {' '}
              algorithm after 16 iterations, when the algorithm reached a fixed point.
            </p>
            <Card>
              <CardContent style={{padding:'0'}}>
                <Grid container spacing={0} alignItems="stretch">
                  <Grid item xs={12} align='center'>
                    <Typography style={{padding:'0', overflowX:'auto'}}>
                      <img src="./images/img - Heater-Sim-1.gif" alt="Heater Simulation" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Heater-Sim-2.gif" alt="Heater Simulation" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Heater-Sim-3.gif" alt="Heater Simulation" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Heater-STC-1.gif" alt="Heater STC Support Function" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Heater-STC-2.gif" alt="Heater STC Support Function" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Heater-STC-3.gif" alt="Heater STC Support Function" style={{width:'350px'}}/>
                    </Typography>
                  </Grid>
                  <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>Images are Generated by SpaceEx</Grid>
                </Grid>
              </CardContent>
            </Card>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}