import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import MathJax from 'react-mathjax2';

export default class ExSinusoid extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        Sinusoid
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
        Sinusoid
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
              One single sine curve. 
              Taken from <Link href={'http://hsolver.sourceforge.net/benchmarks/sinusoid.html'} target='_blank' rel='noreferrer'>HSolver benchmarks</Link>.
            </p>
            <h3>Dynamics</h3>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 1</h4></td>
              </tr>
              <tr>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{                               \n' +
                          '\\begin{array}{rl}                      \n' +
                          '-3  \\!\\!\\!\\!&\\leq x \\leq 3  \\\\  \n' +
                          '0   \\!\\!\\!\\!&\\leq t \\leq 10 \\\\  \n' +
                          '\\end{array}                            \n' +
                          '\\right.                                \n'
                         )}
                </td>
              </tr>
              <tr>
                <td>Flow:</td>
                <td>{mjax('\\left\\{                 \n' +
                          '\\begin{array}{l}         \n' +
                          '\\dot x = cos(t)   \\\\   \n' +
                          '\\dot t = 1        \\\\   \n' +
                          '\\end{array}              \n' +
                          '\\right.                  \n'
                         )}
                </td>
              </tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Initial States</h4></td>
              </tr>
              <tr><td>Mode 1:     </td><td>{mjax('x=0 \\wedge t=0')}</td></tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Unsafe States</h4></td>
              </tr>
              <tr><td>Mode 1:     </td><td>{mjax('x \\leq -2 \\vee x\\geq 2')}</td></tr>
            </table>
            <h4>Simulation</h4>
            <Card>
              <CardContent style={{padding:'0'}}>
                <Grid container spacing={0} alignItems="stretch">
                  <Grid item xs={12} align='center'>
                    <Typography style={{padding:'0', overflowX:'auto'}}>
                      <img src="./images/img - Sinusoid.png" alt="Sinusoid Simulation"/>
                    </Typography>
                  </Grid>
                  <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>Simulation Generated by Matlab<sup>&reg;</sup></Grid>
                </Grid>
              </CardContent>
            </Card>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}

function mjax(fml) {
  return  (
    <MathJax.Context input='tex'>
      <MathJax.Node inline>{fml}</MathJax.Node>
    </MathJax.Context>
  );
}