import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import MathJax from 'react-mathjax2';

export default class ExRealEigen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        RealEigen
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
        RealEigen
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
              A linear, three-dimensional example. 
              Taken from <Link href={'http://hsolver.sourceforge.net/benchmarks/benchmarks_16.html'} target='_blank' rel='noreferrer'>HSolver benchmarks</Link>.
            </p>
            <h3>Dynamics</h3>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 1</h4></td>
              </tr>
              <tr>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{                                         \n' +
                          '\\begin{array}{rl}                                \n' +
                          '5.3128  \\!\\!\\!\\!&\\leq x_1 \\leq 14.3128 \\\\ \n' +
                          '20.7321 \\!\\!\\!\\!&\\leq x_2 \\leq 29.7321 \\\\ \n' +
                          '4.3571  \\!\\!\\!\\!&\\leq x_3 \\leq 13.3571 \\\\ \n' +
                          '\\end{array}                                      \n' +
                          '\\right.                                          \n'
                         )}
                </td>
              </tr>
              <tr>
                <td>Flow:</td>
                <td>{mjax('\\left\\{                         \n' +
                          '\\begin{array}{l}                 \n' +
                          '\\dot x_1 = 0.8x_2 + 0.6x_3  \\\\ \n' +
                          '\\dot x_2 = 0.8x_1 + 0.7x_3  \\\\ \n' +
                          '\\dot x_3 = 0.6x_1 + 0.7x_2  \\\\ \n' +
                          '\\end{array}                      \n' +
                          '\\right.                          \n'
                         )}
                </td>
              </tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Initial States</h4></td>
              </tr>
              <tr><td>Mode 1:</td><td>{mjax('19 - 9.6872 \\leq x_1 \\leq 20 - 9.6872 \\wedge  \n' +
                                            '19 + 5.7321 \\leq x_2 \\leq 20 + 5.7321 \\wedge  \n' +
                                            '19 - 10.6429\\leq x_3 \\leq 20 - 10.6429')}</td></tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Unsafe States</h4></td>
              </tr>
              <tr><td>Mode 1:     </td><td>{mjax('x_1 \\leq 11.3128 \\wedge \n' +
                                                 'x_2 \\leq 25.7321 \\wedge \n' +
                                                 'x_3 \\geq 11.8571')}</td></tr>
            </table>
            <h4>Simulation</h4>
            <Card>
              <CardContent style={{padding:'0'}}>
                <Grid container spacing={0} alignItems="stretch">
                  <Grid item xs={12} align='center'>
                    <Typography style={{padding:'0', overflowX:'auto'}}>
                      <img src="./images/img - RealEigen.png" alt="RealEigen Simulation"/>
                    </Typography>
                  </Grid>
                  <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>Simulation Generated by Matlab<sup>&reg;</sup></Grid>
                </Grid>
              </CardContent>
            </Card>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}

function mjax(fml) {
  return  (
    <MathJax.Context input='tex'>
      <MathJax.Node inline>{fml}</MathJax.Node>
    </MathJax.Context>
  );
}