import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import MathJax from 'react-mathjax2';

export default class ExTank extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        Water Tank
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
        Water Tank
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
              Water Tank is another well-known example in the world of hybrid automata benchmarks
              (see {' '}
               <Link href="https://doi.org/10.1007/3-540-63141-0_6"                           target='_blank' rel='noreferrer'>Alur et. al.</Link>, {' '}
               <Link href="https://doi.org/10.1016/S0167-6911(99)00059-6"                     target='_blank' rel='noreferrer'>Johansson et. al.</Link>, and {' '}
               <Link href="https://swt.informatik.uni-freiburg.de/tool/spaceex/guided-search" target='_blank' rel='noreferrer'>SpaceEx Guided Search</Link> {' '}
               for examples).
              There are {mjax('N\\in\\mathbb{N}')} tankes, where each tank {mjax('i \\in \\{1,\\ldots,N\\}')} {' '} 
              loses volume {mjax('x_i')} at some constant flow rate {mjax('v_i')}, so tank {mjax('i')} has dynamics {' '} 
              {mjax('\\dot x_i=-v_i')}, for a real constant {mjax('v_i\\geq0')}. One of the tanks is filled from an 
              external inlet at some constant flow rate {mjax('w')}, so it has dynamics {mjax('\\dot x_i=w-v_i')}, 
              for a real constant {mjax('w\\geq0')}. 
              In {' '}
              <Link href="https://swt.informatik.uni-freiburg.de/tool/spaceex/guided-search" target='_blank' rel='noreferrer'>SpaceEx Guided Search</Link> {' '}
              variant that we consider in our examples, 
              the volume lost by each tank simply vanishes and does not move from one tank to another. 
              This benchmark class is qualitatively different than either the Navigation or Satellite benchmarks, 
              as the discrete state space may be small. However, there is nondeterminism in which tank to fill at 
              a given instance.
              Please see <Link href="https://doi.org/10.1016/S0167-6911(99)00059-6" target='_blank' rel='noreferrer'>Johansson et. al.</Link> {' '}
              for more detail.
            </p>
            <p align='justify'>
              The following figure shows the general idea in a water tank system.
            </p>
            <Card>
            <CardContent style={{padding:'0'}}>
              <Grid container spacing={0} alignItems="stretch">
                <Grid item xs={12} align='center'>
                  <Typography style={{padding:'0', overflowX:'auto'}}>
                    <img src="./images/img - Tank - Ex.png" alt="Tank Example" style={{width:'250px'}}/>
                  </Typography>
                </Grid>
                <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>
                  Borrowed from {' '}
                  <Link href="https://doi.org/10.1016/S0167-6911(99)00059-6" target='_blank' rel='noreferrer' style={{color:'white'}}>On the regularization of Zeno hybrid automata, by Johansson et. al.</Link>
                </Grid>
              </Grid>
            </CardContent>
            </Card>
            <p align='justify'>
              We have a total of 17 Tank examples in our benchmarks.
              Each model has 3 to 8 locations, 4 to 15 edges, and 3 to 8 continuous variables.
              There are initial and unsafe sets in every location.
            </p>
            <h4 style={{marginBottom:'0'}}>Simulation</h4>
            <p align="justify">
              We used <Link href='http://spaceex.imag.fr/' target='_blank' rel='noreferrer'>SpaceEx</Link> and the Tank-10 problem for this section.
              The first three figures below show the simulation of three executions (there are exactly three initial states in this example). 
              We set local time horizon and sampling time both to 100000. 
              The tool found fixed point after 45 iterations.
              The last three figures show the reachable set using {' '}
              <Link href="http://spaceex.imag.fr/news/new-analysis-algorithm-stc-scenario-released-47" target='_blank' rel='noreferrer'>STC Support Function</Link> {' '}
              algorithm after 1000 iterations.
              The tool did not reach a fixed point.
              We also changed 'Direction' parameter to 'uni 16', so the result won't be too much over-approximated.
            </p>
            <Card>
              <CardContent style={{padding:'0'}}>
                <Grid container spacing={0} alignItems="stretch">
                  <Grid item xs={12} align='center'>
                    <Typography style={{padding:'0', overflowX:'auto'}}>
                      <img src="./images/img - Tank-Sim-1.gif" alt="Tank Simulation" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Tank-Sim-2.gif" alt="Tank Simulation" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Tank-Sim-3.gif" alt="Tank Simulation" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Tank-STC-1.gif" alt="Tank STC Support Function" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Tank-STC-2.gif" alt="Tank STC Support Function" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Tank-STC-3.gif" alt="Tank STC Support Function" style={{width:'350px'}}/>
                    </Typography>
                  </Grid>
                  <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>Images are Generated by SpaceEx</Grid>
                </Grid>
              </CardContent>
            </Card>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}

function mjax(fml) {
  return  (
    <MathJax.Context input='tex'>
      <MathJax.Node inline>{fml}</MathJax.Node>
    </MathJax.Context>
  );
}