
import React, { Component } from 'react';

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import Fade from '@material-ui/core/Fade';

import { Link as RouterLink } from 'react-router-dom';

import HugeTable from './cmpHugeTable'
import ExVanDerPol from './cmpExVanDerPol'
import ExSinusoid from './cmpExSinusoid'
import ExRealEigen from './cmpExRealEigen';
import ExCycle from './cmpExCycle';
import ExFocus from './cmpExFocus';
import ExClock from './cmpExClock';
import ExCircuit from './cmpExCircuit';
import ExConvoi1 from './cmpExConvoi1';
import ExOneFlow from './cmpExOneFlow';
import ExFischer from './cmpExFischer';
import ExNavigation from './cmpExNavigation';
import ExSatellite from './cmpExSatellite';
import ExHeater from './cmpExHeater';
import ExTank from './cmpExTank';

export default class Benchmarks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      index: 0
    };
    setTimeout(this.increaseIndex, 100)
  }

  increaseIndex = () => {
    var index = this.state.index + 1;
    this.setState({index: index});
    if(index < 20) // 20 is larger than number of buttons
      setTimeout(this.increaseIndex, 100)
  }

  render = () => {
    var minIndex = 0
    return (
    <Typography component="div" align='justify' style={{ padding: 8 * 3 }}>
      <Typography component="div" align='justify' style={{ paddingBottom: 8 * 3 }}>
        We ran HARE on 85 examples. The following table shows many performance metrics that we gathered during the execution of those examples
        (all times are in seconds). 
        The table has 38 columns, so make sure you scroll in both directions. All columns are sortable, to make the comparison easier. 
        Also, to make the name of each column shorter, we use the following abbreviations: {' '} 
        <em>MC</em> stands for model checking, {' '} 
        <em>CE</em> stands for counterexample, {' '} 
        <em>NLF</em> stands for hybrid automata with non-linear flows and polyhedral constraints (a.k.a., concrete models), and {' '} 
        <em>Poly</em> stands for hybrid automata with polyhedral constraints (a.k.a., abstract models).
        Furthermore, hovering over almost any header in this table shows a tool-tip about the corresponding column in the table. 
        Finally, clicking on the name of each example takes you to that example in the repository. 
        Last but not least, Experimental Results sections in our <Link component={RouterLink} to="/publications">TACAS</Link> {' '}
        papers compare other state-of-the-art tools with HARE.
      </Typography>
      <Typography component="div" align='justify' style={{ paddingBottom: 8 * 3 }}>
        <Grid container spacing={2} justify="center">
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExNavigation/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExSatellite/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExHeater/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExTank/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExVanDerPol/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExSinusoid/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExRealEigen/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExCycle/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExFocus/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExClock/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExCircuit/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExConvoi1/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExOneFlow/></Grid></Fade>
          <Fade in={this.state.index>=minIndex++} {...{ timeout: 2000 }}><Grid item><ExFischer/></Grid></Fade>
        </Grid>
      </Typography>
    { this.state.index>=minIndex ? <HugeTable/> : '' }
    </Typography>
    )
  };
}
