
import React, { Component } from 'react';

import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';

import Grow from '@material-ui/core/Grow';

import { Link as RouterLink } from 'react-router-dom';

export default class Home extends Component {

  render = () => {
    return (
  <Typography component="div" style={{ padding: 8 * 3 }}>
    <Typography component="div" align="justify" gutterBottom>
      HARE (Hybrid Abstraction-Refinement Engine) is a counterexample guided abstraction-refinement (CEGAR) based tool to verify safety properties of hybrid automata, 
      whose continuous dynamics in each mode is non-linear, but initial values, invariants, and transition relations are specified using polyhedral constraints. HARE 
      works by abstracting non-linear hybrid automata into hybrid automata with polyhedral inclusion dynamics and uses {' '}
      <Link href='http://dreal.github.io/dReach' target='_blank' rel='noreferrer'>dReal</Link> to validate counterexamples. 
      We have shown that the CEGAR framework forming the theoretical basis of HARE, makes provable progress in each iteration of the abstraction-refinement loop. 
      The current HARE tool is a significant advance on previous versions of HARE &#8212; it considers a richer class of abstract models (polyhedral flows as opposed to 
      rectangular flows), and can be applied to a larger class of concrete models (non-linear hybrid automata as opposed to affine hybrid automata). These advances 
      have led to better performance results for a broader class of examples. {' '}
      <Link component={RouterLink} to="/benchmarks">Benchmarks</Link> page report an experimental comparison of HARE against other state-of-the-art tools for affine 
      models (
        <Link href='http://spaceex.imag.fr'                                   target='_blank' rel='noreferrer'>SpaceEx</Link>, {' '}
        <Link href='http://spaceex.imag.fr/phaver-8'                          target='_blank' rel='noreferrer'>PHAVer</Link>, and {' '}
        <Link href='https://swt.informatik.uni-freiburg.de/tool/spaceex/agar' target='_blank' rel='noreferrer'>SpaceEx AGAR</Link>) and 
      non-linear models (
        <Link href='https://flowstar.org/'                  target='_blank' rel='noreferrer'>FLOW*</Link>,        {' '}
        <Link href='http://hsolver.sourceforge.net/'        target='_blank' rel='noreferrer'>HSolver</Link>, and  {' '}
        <Link href='http://publish.illinois.edu/c2e2-tool/' target='_blank' rel='noreferrer'>C2E2</Link>).
    </Typography>

    <Typography component="div" align="justify" gutterBottom>
      The following figure shows the flow and architecture of HARE. 
      It also identifies 3rd party libraries/tools that are internally used by HARE at different steps. 
      We use <Link href='https://github.com/Z3Prover/z3' target='_blank' rel='noreferrer'>Z3</Link> to check if a fix-point is reached in the abstract system model-checking, 
      and also to check whether an unsafe state is reached. We use <Link href='https://www.boost.org/doc/libs/1_71_0/libs/numeric/interval/doc/interval.htm' target='_blank' 
      rel='noreferrer'>Boost Interval Arithmetic Library (IAL)</Link> to abstract non-linear dynamics. We use <Link href='http://dreal.github.io/dReach' target='_blank' 
      rel='noreferrer'>dReach</Link> to validate a counterexample (the validation a counterexample of length n involves at most n invocations of dReach). Finally, we use {' '}
      <Link href='https://www.bugseng.com/ppl' target='_blank' rel='noreferrer'>Parma Polyhedra Library (PPL)</Link> to manipulate symbolic abstract states. 
      This includes
        computing discrete/continuous abstract posts, 
        constructing annotated counterexamples, 
        finding rectangular hull of a polytope, 
        abstracting affine flows, and 
        checking if a parallelly composed location/edge has non-empty invariant/transition relation. 
      Compared to the old version of HARE, we have replaced {' '}
      <Link href='http://spaceex.imag.fr' target='_blank' rel='noreferrer'>SpaceEx</Link> with {' '}
      <Link href='http://dreal.github.io/dReach' target='_blank' rel='noreferrer'>dReach</Link>, since the former does not support non-linear dynamics. 
      Also, we have implemented everything in C++ instead of Scala to improve performance.
    </Typography>

    <p/>
    
    <Grow in={true} {...{ timeout: 1000 }}>
      <Typography component="div" align="center" gutterBottom style={{overflowX:'auto'}}>
        <img src={process.env.PUBLIC_URL + "/images/img-architecture.png"} alt="Flow Chart of HAREs CEGAR Loop" width='800'/>
      </Typography>
    </Grow>
  </Typography>
    );
  }
}
