import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import MathJax from 'react-mathjax2';

export default class ExFischer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        Fischer
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
        Fischer
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
              This is a protocol to guarantee mutual exclusion in a concurrent real-time systems consisting of a number of processors
              (see <Link href="https://doi.org/10.1007/3-540-60249-6_41" target='_blank' rel='noreferrer'>Model-checking for real-time systems, 
              by Larsen et. al.</Link>, for more detail).
              In our experiments, we consider cases in which there are {' '}
              <Link href={'https://github.com/nima-roohi/HARE/blob/master/benchmarks/HSolver/fischer2.prb'} target='_blank' rel='noreferrer'>two</Link> and {' '}
              <Link href={'https://github.com/nima-roohi/HARE/blob/master/benchmarks/HSolver/fischer3.prb'} target='_blank' rel='noreferrer'>three</Link> clients,
              both of them are 
              taken from <Link href={'http://hsolver.sourceforge.net/benchmarks/benchmarks_12.html'} target='_blank' rel='noreferrer'>HSolver benchmarks</Link>.
            </p>
            <h3 style={{marginBottom:'0'}}>Dynamics</h3>
            <p align='justify'>
              The number of locations are too high to explicitly write them here 
              (48 for two clients and 256 for three clients).
              When there are two   clients, automaton has 2 stopwatches: {mjax('t_1,t_2')}, and 
              when there are three clients, automaton has 3 stopwatches: {mjax('t_1,t_2,t_3')}.
              Invariant in all locations enforces stopwatches to be between 0 and 4.
              Also begin a <em>stopwatch</em> means, in every location, a variable is either constant or behaves like a clock.
            </p>
            <p align='justify'>
              There are 81 edges when there are two clients, and 625 edges when there are three clients. 
              Also, very edge either resets a clock to zero or don't change its value.
            </p>
            <p align='justify'>
              Initially all variables are zero, and there is only one initial location.
              Also, there are 5 unsafe locations when there are two clients, and 
              43 unsafe locations where there are three clients.
            </p>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}

function mjax(fml) {
  return  (
    <MathJax.Context input='tex'>
      <MathJax.Node inline>{fml}</MathJax.Node>
    </MathJax.Context>
  );
}