import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import MathJax from 'react-mathjax2';

export default class ExNavigation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        Navigation
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
        Navigation
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
              Navigation is a famous example in the world of hybrid automata benchmarks
              (see {' '}
               <Link href="https://doi.org/10.1145/1132357.1132363"      target='_blank' rel='noreferrer'>Alur et. al.</Link> and {' '}
               <Link href="https://doi.org/10.1007/978-3-540-24743-2_22" target='_blank' rel='noreferrer'>Fehnker et. al.</Link> {' '}
               for examples).
              A robot moves in the bounded rectangle of {mjax('\\mathbb{R}^2')} plane, where
              the plane is divided into a {mjax('n\\times m')} grid with equal sizes.
              Each cell in this grid has a desired velocity {mjax('v_d')}.
              Acceleration is defined by {mjax('\\dot v=A\\times (v-v_d)')}, where {mjax('v')} is the current velocity and {mjax('A')} is a 2x2
              matrix. It is generally assumed that eigenvalues of {mjax('A')} have negative real parts. 
              This is to guarantee velocity always converges to its desired value.
            </p>
            <p align='justify'>
              The following figures show two examples with a few sample trajectories. 
              In each of these examples we have a 5x5 grid.
              Each cell in these grids shows the desired velocity in that cell.
            </p>
            <Card>
            <CardContent style={{padding:'0'}}>
              <Grid container spacing={0} alignItems="stretch">
                <Grid item xs={12} align='center'>
                  <Typography style={{padding:'0', overflowX:'auto'}}>
                    <img src="./images/img - Nav - 1.png" alt="Navigation Example 1" style={{width:'300px'}}/>
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <img src="./images/img - Nav - 2.png" alt="Navigation Example 2" style={{width:'300px'}}/>
                  </Typography>
                </Grid>
                <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>
                  Borrowed from {' '}
                  <Link href="https://doi.org/10.1007/978-3-540-24743-2_22" target='_blank' rel='noreferrer' style={{color:'white'}}>Benchmarks for Hybrid Systems Verification, by Fehnker et. al.</Link>
                </Grid>
              </Grid>
            </CardContent>
            </Card>
            <p align='justify'>
              We have a total of 36 Navigation examples in our benchmarks.
              They come in variety of sizes.
              The smallest ones have only 4 locations, while the largest ones have 99 locations.
              Also, the smallest example have only 8 edges, while the largest ones have 317 edges.
              The other major difference in these examples is that in some of them a discrete transition can be only taken when the robot 
              is exactly on the border of two cells, while in some of the other examples, a discrete transition can be taken while the robot 
              is close to the border. 
              Interior of the neighbor cells have empty intersection in the first case and non-empty intersection in the second case.
            </p>
            <h4 style={{marginBottom:'0'}}>Simulation</h4>
            <p align="justify">
              We used <Link href='http://spaceex.imag.fr/' target='_blank' rel='noreferrer'>SpaceEx</Link> and arbitrary Navigation problem for this section.
              The first figure below shows 1000 executions with random initial state. We set local time horizon to 10 and 
              sampling time to 1000. The tool found a fixed point after 26 iterations.
              The second figure shows the reachable set using {' '}
              <Link href="http://spaceex.imag.fr/news/new-analysis-algorithm-stc-scenario-released-47" target='_blank' rel='noreferrer'>STC Support Function</Link> {' '}
              algorithm after 200 iterations and with the same local time horizon.
              The algorithm did not reach a fixed point.
            </p>
            <Card>
              <CardContent style={{padding:'0'}}>
                <Grid container spacing={0} alignItems="stretch">
                  <Grid item xs={12} align='center'>
                    <Typography style={{padding:'0', overflowX:'auto'}}>
                      <img src="./images/img - Nav-Sim.gif" alt="Navigation Simulation" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Nav-STC.gif" alt="Navigation STC Support Function" style={{width:'350px'}}/>
                    </Typography>
                  </Grid>
                  <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>Images are Generated by SpaceEx</Grid>
                </Grid>
              </CardContent>
            </Card>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}

function mjax(fml) {
  return  (
    <MathJax.Context input='tex'>
      <MathJax.Node inline>{fml}</MathJax.Node>
    </MathJax.Context>
  );
}