import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import MathJax from 'react-mathjax2';

export default class ExCycle extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        Cycle
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
        Cycle
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
              Simple two-dimensional example with cyclic behavior in the discrete state space. 
              The dynamics in this model is quite trivial. However, there is a Zeno behavior in the model and edges can be taken at any time.
              Taken from <Link href={'http://hsolver.sourceforge.net/benchmarks/benchmarks_20.html'} target='_blank' rel='noreferrer'>HSolver benchmarks</Link>.
            </p>
            <h3>Dynamics</h3>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 1</h4></td>
                <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 2</h4></td>
              </tr>
              <tr>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{               \n' +
                          '\\begin{array}{l}       \n' +
                          '0 \\leq x \\leq 1 \\\\  \n' +
                          '0 \\leq y \\leq 1 \\\\  \n' +
                          '\\end{array}            \n' +
                          '\\right.                \n'
                         )}
                </td>
                <td/>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{               \n' +
                          '\\begin{array}{l}       \n' +
                          '0 \\leq x \\leq 1 \\\\  \n' +
                          '0 \\leq y \\leq 1 \\\\  \n' +
                          '\\end{array}            \n' +
                          '\\right.                \n'
                         )}
                </td>
              </tr>
              <tr>
                <td>Flow:</td>
                <td>{mjax('\\left\\{               \n' +
                          '\\begin{array}{l}       \n' +
                          '\\dot x = 0       \\\\  \n' +
                          '\\dot y = 1       \\\\  \n' +
                          '\\end{array}            \n' +
                          '\\right.                \n'
                         )}
                </td>
                <td/>
                <td>Flow:</td>
                <td>{mjax('\\left\\{               \n' +
                          '\\begin{array}{l}       \n' +
                          '\\dot x = 0       \\\\  \n' +
                          '\\dot y = 1       \\\\  \n' +
                          '\\end{array}            \n' +
                          '\\right.                \n'
                         )}
                </td>
              </tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Edge</h4></td>
              </tr>
              <tr><td>Source:     </td><td>Mode 1</td></tr>
              <tr><td>Destination:</td><td>Mode 2</td></tr>
              <tr><td>Relation:</td><td>{mjax('x\'=x')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('y\'=y+0.1')}</td></tr>
              <tr><td>&nbsp;</td></tr>
              <tr><td>Source:     </td><td>Mode 2</td></tr>
              <tr><td>Destination:</td><td>Mode 1</td></tr>
              <tr><td>Relation:</td><td>{mjax('x\'=x')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('y\'=y')}</td></tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Initial States</h4></td>
              </tr>
              <tr><td>Mode 1:     </td><td>{mjax('x<0.5')}</td></tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Unsafe States</h4></td>
              </tr>
              <tr><td>Mode 1:     </td><td>{mjax('x>0.6')}</td></tr>
            </table>
            <p>
              Looking at the dynamics it is easy to see that the set of reachable states in Mode 1 is {mjax('0\\leq x<0.5\\wedge 0\\leq y\\leq 1')},
              and in Mode 2 is {mjax('0\\leq x<0.5\\wedge 0.1\\leq y\\leq 1')}.
            </p>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}

function mjax(fml) {
  return  (
    <MathJax.Context input='tex'>
      <MathJax.Node inline>{fml}</MathJax.Node>
    </MathJax.Context>
  );
}