//-------------------------------------------------------------------------------------------------------------------------------------------------------------
//---- S O R T I N G - M E C H A N I S M ----------------------------------------------------------------------------------------------------------------------
//-------------------------------------------------------------------------------------------------------------------------------------------------------------

function desc(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) return -1;
  if (b[orderBy] > a[orderBy]) return  1;
  return 0;
}

function stableSort(array, cmp) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = cmp(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map(el => el[0]);
}

function getSorting(order, orderBy) {
  return order === 'desc' ? (a, b) => desc(a, b, orderBy) : (a, b) => -desc(a, b, orderBy);
}

//-------------------------------------------------------------------------------------------------------------------------------------------------------------
//---- T A B L E - H E A D E R --------------------------------------------------------------------------------------------------------------------------------
//-------------------------------------------------------------------------------------------------------------------------------------------------------------

function headLabel(column) {
  return (
    <div style={{ minWidth: column.minWidth, maxWidth: column.maxWidth }}>
        {column.label ? column.label : column.id}
    </div>
  ); 
}

function EnhancedTableHead(props) {
  const { order, orderBy, onRequestSort } = props;
  const createSortHandler = property => event => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        {columns.map((column, index) => (
          <TableCell
            key={column.id}
            sortDirection={orderBy === index ? order : false}
            align={column.kind === 'S' ? 'left' : 'center'}
            style={{ padding: 3 }}
          >
            <TableSortLabel
              active={orderBy === column.col_id}
              hideSortIcon
              direction={order}
              onClick={createSortHandler(column.col_id)}
              style={{ padding: 0, margin: 0 }}
            >
              {
                column.tooltip ? 
                <Tooltip title={column.tooltip}>{ headLabel(column) }</Tooltip> :
                headLabel(column)
              }
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

EnhancedTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order:         PropTypes.oneOf(['asc', 'desc']).isRequired,
  orderBy:       PropTypes.number.isRequired,
};

//-------------------------------------------------------------------------------------------------------------------------------------------------------------
//---- T A B L E ----------------------------------------------------------------------------------------------------------------------------------------------
//-------------------------------------------------------------------------------------------------------------------------------------------------------------

function encode_value(value, kind) {
  switch(kind) {
    case 'T': return Math.ceil(value / 100.0) / 10.0;
    case 'B': { const lc = value.toLowerCase(); return lc === 'true' || lc === 'safe' ? Yes : lc === 'false' || lc === 'unsafe' ?  No : null; }
    case 'S': return value;
    case 'I': return value;
    case 'N': return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    default:
      return value;
    }
}

function EnhancedTable(props) {
  const [order, setOrder]     = React.useState('desc');
  const [orderBy, setOrderBy] = React.useState(0);
  const handleRequestSort = (event, property) => {
    const isDesc = orderBy === property && order === 'desc';
    setOrder(isDesc ? 'asc' : 'desc');
    setOrderBy(property);
  };
  const transform = (row, index) => {
    return (
      <TableRow hover tabIndex={-1} key={index}>
      { columns.map(column => {
        const idx   = column.col_id;
        const value = row[idx];
        return (
          <TableCell key={idx} align={column.align} style={{ padding: 7, background: idx < 1 ? '#FAFAFA' : null}}>
            {
              column.format && typeof value === 'number' ? column.format(value) : 
              idx !== 1 ? encode_value(value, column.kind) :
              <Link href={'https://github.com/nima-roohi/HARE/tree/master/benchmarks/' + row[2]} target='_blank' rel='noreferrer'>{value}</Link>
            }
          </TableCell>
        );
      })}
      </TableRow>);
  };
  return (
    <Table stickyHeader size='small'>
      <EnhancedTableHead
        order={order}
        orderBy={orderBy}
        onRequestSort={handleRequestSort}
      />
      <TableBody>
        { stableSort(props.data, getSorting(order, orderBy)).map(transform) }
      </TableBody>
    </Table>);
}

//-------------------------------------------------------------------------------------------------------------------------------------------------------------
//---- P U T T I N G - T H I N G S - T O G E T H E R ----------------------------------------------------------------------------------------------------------
//-------------------------------------------------------------------------------------------------------------------------------------------------------------

export default class HugeTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      results: undefined,
      failedMsg: undefined,
      vw: window.innerWidth * 0.01,
    };
  }

  updateDimensions = () => {
    const minWidth = parseInt(document.body.style.minWidth,10);
    const width = window.innerWidth;
    const vw = Math.max(minWidth, width) * 0.01;
    this.setState({ vw: vw });
  };
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensions);
  }

  componentDidMount() {
    window.addEventListener('resize', this.updateDimensions);
    fetch(process.env.PUBLIC_URL + '/benchmarks/metrics.json')
      .then(response => {
        if (!response.ok)
          throw Error('./' + response.statusText);
        return response;
      })
      .then((r) => r.json())
      .then(js => {
        this.setState({
          isLoading: false,
          results: js.data
        })
      })
      .catch(e => this.setState({
        isLoading: false,
        failedMsg: e
      }))
  }

  render = () => {
    document.documentElement.style.setProperty('--vw', `${this.state.vw}px`);
    const vw = 'calc(var(--vw, 1vw) * 100 - 48px)';
    return (
          this.state.failedMsg ? <ErrorMessage msg={'Error in loading benchmark results file.'} extra={this.state.failedMsg.toString()} /> :
          this.state.isLoading ? <Typography component="div" align='center'><br/><CircularProgress color="secondary"/><br/></Typography> :
          <Slide in={true} timeout={2000} direction="up"> 
          <Paper component="div" style={{  overflowX:'auto', overflowY:'auto', position:'absolute', width:vw, height:'100%'}}>
            <EnhancedTable data={this.state.results} />
          </Paper>
          </Slide>
    );
  }
}
