import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

export default class ExSatellite extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        Satellite
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
        Satellite
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
              Satellite is a famous example in the world of hybrid automata benchmarks
              (see {' '}
               <Link href="https://w3.onera.fr/smac/satelliteModel"          target='_blank' rel='noreferrer'>Systems Modeling Analysis and Control</Link>, {' '}
               <Link href="https://doi.org/10.1007/978-3-642-32759-9_22"     target='_blank' rel='noreferrer'>Johnson et. al.</Link>, {' '}
               <Link href="https://doi.org/10.3182/20080706-5-KR-1001.01889" target='_blank' rel='noreferrer'>Tantawi et. al.</Link>,  and {' '}
               <Link href="https://doi.org/10.1115/1.4025998"                target='_blank' rel='noreferrer'>Guy et. al.</Link> {' '}
               for examples).
              Two satellites orbit the earth with nonlinear dynamics described by 
              Kepler's laws. Please look at {' '}
              <Link href="https://swt.informatik.uni-freiburg.de/tool/spaceex/guided-search" target='_blank' rel='noreferrer'>SpaceEx Guided Search</Link> and {' '}
              <Link href="https://doi.org/10.1007/978-3-642-32759-9_22" target='_blank' rel='noreferrer'>Johnson et. al.</Link> {' '}
              for more detail.
              The verification problem is the conjunction avoidance, i.e., to determine whether there 
              exists a trajectory where the satellites come too close to one another and may collide.
            </p>
            <p align='justify'>
              We have a total of 15 Satellite examples in our benchmarks.
              They come in variety of sizes.
              The smallest ones have only 36 locations, while the largest ones have 1296 locations.
              Also, the smallest example have only 114 edges, while the largest ones have 3895 edges.
              All instances feature 4 continuous variables. 
            </p>
            <h4 style={{marginBottom:'0'}}>Simulation</h4>
            <p align="justify">
              We used <Link href='http://spaceex.imag.fr/' target='_blank' rel='noreferrer'>SpaceEx</Link> and the first Satellite problem for this section.
              The first figure below shows 1000 executions with random initial state. We set local time horizon to 5 and 
              sampling time to 10. The tool did not reach a fixed point after 500 iterations.
              The second figure shows the reachable set using {' '}
              <Link href="http://spaceex.imag.fr/news/new-analysis-algorithm-stc-scenario-released-47" target='_blank' rel='noreferrer'>STC Support Function</Link> {' '}
              algorithm after 500 iterations and with the same local time horizon.
              The algorithm did not reach a fixed point.
            </p>
            <Card>
              <CardContent style={{padding:'0'}}>
                <Grid container spacing={0} alignItems="stretch">
                  <Grid item xs={12} align='center'>
                    <Typography style={{padding:'0', overflowX:'auto'}}>
                      <img src="./images/img - Satellite-Sim.gif" alt="Satellite Simulation" style={{width:'350px'}}/>
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <img src="./images/img - Satellite-STC.gif" alt="Satellite STC Support Function" style={{width:'350px'}}/>
                    </Typography>
                  </Grid>
                  <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>Images are Generated by SpaceEx</Grid>
                </Grid>
              </CardContent>
            </Card>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}
