import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import MathJax from 'react-mathjax2';

export default class ExVanDerPol extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        Van der Pol
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
          Van der Pol
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
            A modified <Link href={'https://en.wikipedia.org/wiki/Van_der_Pol_oscillator'} target='_blank' rel='noreferrer'>Van der Pol</Link> equation 
            with a time variable and some discrete jumps between different modes. A Van der Pole equation is a fundamental, 2-dimensional example in 
            nonlinear oscillation theory. 
            Taken from <Link href={'http://hsolver.sourceforge.net/benchmarks/benchmarks_19.html'} target='_blank' rel='noreferrer'>HSolver benchmarks</Link>.
            </p>
            <h3>Dynamics</h3>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 1</h4></td>
                <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 2</h4></td>
              </tr>
              <tr>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{                                 \n' +
                          '\\begin{array}{ll}                        \n' +
                          '-2   \\!\\!\\!\\!&\\leq x_1 \\leq 2 \\\\  \n' +
                          '0.01 \\!\\!\\!\\!&\\leq x_2 \\leq 2 \\\\  \n' +
                          '0    \\!\\!\\!\\!&\\leq x_3 \\leq 6 \\\\  \n' +
                          '\\end{array}                              \n' +
                          '\\right.                                  \n'
                         )}
                </td>
                <td/>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{                      \n' +
                          '\\begin{array}{l}              \n' +
                          '-2 \\leq x_1 \\leq 2     \\\\  \n' +
                          '-2 \\leq x_2 \\leq -0.01 \\\\  \n' +
                          '0  \\leq x_3 \\leq 6     \\\\  \n' +
                          '\\end{array}                   \n' +
                          '\\right.                       \n'
                         )}
                </td>
              </tr>
              <tr>
                <td>Flow:</td>
                <td>{mjax('\\left\\{                                \n' +
                          '\\begin{array}{l}                        \n' +
                          '\\dot x_1 = -x_2                   \\\\  \n' +
                          '\\dot x_2 = x_1 - 2(1 - x_1^2)x_2  \\\\  \n' +
                          '\\dot x_3 = 1                      \\\\  \n' +
                          '\\end{array}                             \n' +
                          '\\right.                                 \n'
                         )}
                </td>
                <td/>
                <td>Flow:</td>
                <td>{mjax('\\left\\{                                \n' +
                          '\\begin{array}{l}                        \n' +
                          '\\dot x_1 = -x_2                   \\\\  \n' +
                          '\\dot x_2 = x_1 - 2(1 - x_1^2)x_2  \\\\  \n' +
                          '\\dot x_3 = 1                      \\\\  \n' +
                          '\\end{array}                             \n' +
                          '\\right.                                 \n'
                         )}
                </td>
              </tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Edge</h4></td>
              </tr>
              <tr><td>Source:     </td><td>Mode 1</td></tr>
              <tr><td>Destination:</td><td>Mode 2</td></tr>
              <tr><td>Relation:</td><td>{mjax('-2 \\leq x_1 < 0')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('0.01 \\leq x_2 \\leq 0.02')}</td></tr>
              <tr><td/>             <td>{mjax('x\'_1 = x_1')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('x\'_2 = -0.02')}&nbsp;&nbsp;&nbsp;{mjax('\\wedge')}&nbsp;&nbsp;&nbsp;{mjax('x\'_3 = x_3')}</td></tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Initial States</h4></td>
              </tr>
              <tr><td>Mode 1:     </td><td>{mjax('0.6 \\leq x_1 \\leq 0.9 \\wedge 0.6 \\leq x_2 \\leq 0.9 \\wedge x_3 = 0')}</td></tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Unsafe States</h4></td>
              </tr>
              <tr><td>Mode 1:     </td><td>{mjax('1 < x_1 \\leq 2 \\wedge 0.01 \\leq x_2 \\leq 2 \\wedge 0 \\leq 0 \\leq x_3 \\leq 6')}</td></tr>
              <tr><td>Mode 2:     </td><td>{mjax('-2 \\leq x_1 < - 1.5 \\wedge -2 \\leq x2 < -1.5025 \\wedge 0 \\leq x3 < 0.375')}</td></tr>
            </table>
            <h4 style={{marginBottom:'0'}}>Simulation</h4>
            <p align='justify'>
              The following image contains 961 random paths. The discrete transition is not taken in any of them. 
              However, many of these executions come very close to the guard of the discrete transition.
            </p>
            <Card>
              <CardContent style={{padding:'0'}}>
                <Grid container spacing={0} alignItems="stretch">
                  <Grid item xs={12} align='center'>
                    <Typography style={{padding:'0', overflowX:'auto'}}>
                      <img src="./images/img - Van der Pol.png" alt="Van der Pol Simulation"/>
                    </Typography>
                  </Grid>
                  <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>Simulation Generated by Matlab<sup>&reg;</sup></Grid>
                </Grid>
              </CardContent>
            </Card>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}

function mjax(fml) {
  return  (
    <MathJax.Context input='tex'>
      <MathJax.Node inline>{fml}</MathJax.Node>
    </MathJax.Context>
  );
}