import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import MathJax from 'react-mathjax2';

export default class ExOneFlow extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false
    };
  }

  render = () => {
    return (
      <Typography component="div" style={{ padding: 0 }}>
      <Button variant="outlined" style={{textTransform: 'none'}} disableFocusRipple={true} onClick={() => { this.setState({opened:true}) }}>
        1-Flow
      </Button>
      <Dialog open={this.state.opened} fullWidth={false} maxWidth='md' onClose={() => { this.setState({opened:false}) }}>
        <DialogTitle style={{background:'gray',color:'white'}}>
        1-Flow
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <p align='justify'>
              This is an example from a paper by <Link href="https://doi.org/10.1007/BFb0055350" target='_blank' rel='noreferrer'>J. Preussig, et. al.</Link>
              Taken from <Link href={'http://hsolver.sourceforge.net/benchmarks/benchmarks_1.html'} target='_blank' rel='noreferrer'>HSolver benchmarks</Link>.
            </p>
            <h3>Dynamics</h3>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Mode 1</h4></td>
              </tr>
              <tr>
                <td>Invariant:</td>
                <td>{mjax('\\left\\{                 \n' +
                          '\\begin{array}{l}         \n' +
                          '0 \\leq x_1 \\leq 2 \\\\  \n' +
                          '0 \\leq x_2 \\leq 2 \\\\  \n' +
                          '0 \\leq x_3 \\leq 4 \\\\  \n' +
                          '\\end{array}              \n' +
                          '\\right.                  \n'
                         )}
                </td>
              </tr>
              <tr>
                <td>Flow:</td>
                <td>{mjax('\\left\\{                \n' +
                          '\\begin{array}{l}        \n' +
                          '\\dot x_1 = 1     \\\\   \n' +
                          '\\dot x_2 = 1     \\\\   \n' +
                          '\\dot x_3 = 1     \\\\   \n' +
                          '\\end{array}             \n' +
                          '\\right.                 \n'
                         )}
                </td>
              </tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Initial States</h4></td>
              </tr>
              <tr><td>Mode 1: </td><td>{mjax('0\\leq x_1\\leq 1 \\wedge x_2=0\\wedge x_3=0')}</td></tr>
            </table>
            <table>
              <tr>
                <td colSpan={2}><h4 style={{marginBottom:'0'}}>Unsafe States</h4></td>
              </tr>
              <tr><td>Mode 1: </td><td>{mjax('0\\leq x_1\\leq 2 \\wedge 1< x_2\\leq 2\\wedge 0<x_3<1')}</td></tr>
            </table>
            <h4>Simulation</h4>
            <Card>
              <CardContent style={{padding:'0'}}>
                <Grid container spacing={0} alignItems="stretch">
                  <Grid item xs={12} align='center'>
                    <Typography style={{padding:'0', overflowX:'auto'}}>
                      <img src="./images/img - 1-Flow.png" alt="1-Flow Simulation"/>
                    </Typography>
                  </Grid>
                  <Grid item xs={12} align='center' style={{background:'black',color:'white'}}>Simulation Generated by Matlab<sup>&reg;</sup></Grid>
                </Grid>
              </CardContent>
            </Card>
          </DialogContentText>
        </DialogContent>
      </Dialog>
      </Typography>
    );
  }
}

function mjax(fml) {
  return  (
    <MathJax.Context input='tex'>
      <MathJax.Node inline>{fml}</MathJax.Node>
    </MathJax.Context>
  );
}